.App{
    display: grid;
    grid-template-columns: 3fr 2fr;
    align-items: start;
    width: 800px;
    margin: 0 auto 0 ;
}
.header{
    text-align: center;
    margin-top: 100px ;
}
*{
    border-radius: 7px;
    /*border: 2px solid lightseagreen;*/
}
.todo-item{
    border: 2px solid dodgerblue;
    margin: 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    /*display: grid;*/
    /*align-items: space-between;*/
}
.todo-item:first-child{
    margin-top: 0;
}
.todo-item.true label{
    text-decoration: line-through;
}
input{
    margin-top: 6px;
}
label{
    display: flex;
    align-content: space-between;
    gap: 5px;
}
.delete{
    margin-left: 10px;
}
.form{
    display: grid;
    gap: 10px;
}
.form button{
    background: none;
    padding: 10px;
    border: 2px solid dodgerblue;
}
.form textarea{
    border: 2px solid dodgerblue;
    resize: none;
    padding: 10px;
}
button.delete{
    background: rgba(darkred, 1);
    color: darkred;
    border: 2px solid darkred;
    text-decoration: none;
}
button:hover, label:hover   {
    cursor: pointer;
}
*:focus{
    outline: none;
}
